import Vue from "vue";
import VueRouter from "vue-router";
import Home from "@/components/Home.vue";
import { findFighterDesign } from "@/data/fighterDesigns.js";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/fighters/:fighter",
    name: "Fighter",
    component: Home,
  },
  {
    path: "/privacy",
    name: "Privacy",
    component: Home,
  },
];

const router = new VueRouter({
  // mode: 'history',
  routes,
});

router.beforeEach((to, from, next) => {
  const fighter = to.params.fighter;
  const design = findFighterDesign(fighter);

  if (to.path == "/") {
    next();
  } else if (to.path == "/privacy") {
    next();
  } else if (design != null) {
    next();
  } else {
    next("/");
  }
});

export default router;
